import React, { useState, useEffect } from 'react'
import Navbar from './Navbar'
import Marqueeslider from './Marqueeslider'
import { motion } from "motion/react"
import { useInView } from "react-intersection-observer";

const Heropanel = () => {
  const [animate, setAnimate] = useState(false);

  useEffect(() => {
    setAnimate(true); // Trigger the animation on mount
  }, []);
  const { ref, inView } = useInView({
    triggerOnce: true, // Animation triggers only once
    threshold: 0, 
  });

  return (
    <>
        <main>
        <Navbar />
            <div className="">
                
            <div className="hero-title wrap-container">
              <div className="hero-heading-container">
              <img className='hero-big-blur-img' src="data:image/svg+xml;base64," width="426px" height="412px"></img>
              <img className='hero-small-blur-img' src="data:image/svg+xml;base64,"></img>
              <motion.h1 className='has-text-centered home-h1 padding-bottom-30'
              initial={{ opacity: 0, y: 250 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{
                    duration: 1,
                    ease: "easeOut", // Ensures a smooth easing effect
                  }}>Digital solutions<br></br>for your Business<span className="highlight-color">.</span></motion.h1>
              <motion.h6 className='has-text-centered'
              initial={{ opacity: 0, y: 100 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{
                    duration: 1,
                    delay: 0.5,
                    ease: "easeOut", // Ensures a smooth easing effect
                  }}>DATA. DESIGN. DEVELOPMENT. MARKETING.</motion.h6>
            </div>
              
            </div>
        </div>
        <Marqueeslider />
        </main>
        
        
    </>
  )
}



export default Heropanel


